<template>
  <div class="container">
    <!-- 프로필 정보는 추후 db와 연결할 예정 -->
    <vue-profile 
    :nickname="`${userInfo.userName}`"
    :bodyPhrase="`관심 장르: ${userInfo.genre} & 목표: ${userInfo.goal}`"
    :socialLinks="myLinks"
    :profileImg="Img"
    coverColor="#5498e9"
    /> 
    <!-- 회원정보 수정 -->
    <!-- <b-button v-b-modal.modal2-prevent-closing class="me-3" @click="getUserData()">회원정보 수정</b-button> -->
      <b-modal
        id="modal2-prevent-closing"
        ref="modal"
        title="회원정보 수정"
        hide-footer
        v-if="modalCheck"
      > 
        <form ref="form">
          <b-form-group
            type="file"
            label="프로필 이미지 등록"
            label-for="profileImg"
          >
            <b-form-file
              id="profileImg"
              accept=".png, .jpg, .jpeg, .gif"
              v-model="user.profileImage"
              plain
            ></b-form-file>
            <div class="mt-3">Selected file: {{ user.profileImage ? user.profileImage : '' }}</div>
          </b-form-group>
        </form>
        <form ref="form">
          <b-form-group
            label="변경할 username"
            label-for="username"
          >
            <b-form-input
              id="username"
              v-model="user.user_name"
              placeholder="변경할 username을 입력해주세요"
            ></b-form-input>
          </b-form-group>
        </form>
        <form ref="form">
          <b-form-group
            label="변경할 비밀번호"
            label-for="user_pw"
          >
            <b-form-input
              type="password"
              id="user_pw"
              v-model="user.user_pw"
              placeholder="변경할 password를 입력해주세요"
            ></b-form-input>
          </b-form-group>
        </form>
        <form ref="form">
          <b-form-group
            label="변경할 비밀번호 확인"
            label-for="user_pw_check"
          >
            <b-form-input
              type="password"
              id="user_pw_check"
              v-model="user.user_pw_check"
              placeholder="변경할 password 재입력"
            ></b-form-input>
          </b-form-group>
        </form>
        <form ref="form">
          <b-form-group
            label="변경할 email"
            label-for="user_email"
          >
            <b-form-input
              type="email"
              id="user_email"
              v-model="user.email"
              placeholder="변경할 e-mail을 입력해주세요(-----@-----.--- 형식)"
            ></b-form-input>
          </b-form-group>
        </form>
        <form ref="form">
          <b-form-group
            label="변경할 관심 장르"
            label-for="user_genre"
          >
            <b-form-input
              type="text"
              id="user_genre"
              v-model="user.genre"
              placeholder="변경할 관심 장르를 입력해주세요"
            ></b-form-input>
          </b-form-group>
        </form>
        <form ref="form">
          <b-form-group
            label="변경할 목표 확인"
            label-for="user_goal"
          >
            <b-form-input
              type="text"
              id="user_goal"
              v-model="user.goal"
              placeholder="변경할 목표를 입력해주세요"
              @keyup.enter="userInfoUpdate()"
            ></b-form-input>
          </b-form-group>
        </form>
        <form>
          <b-form-group
            label="변경할 SNS 링크"
            label-for="user_socialLink"
          >
            <b-form-input
              type="url"
              id="user_socialLink"
              v-model="user.socialLink"
              placeholder="변경할 SNS링크를 입력해주세요(https://----.--- 형식)"
              @keyup.enter="userInfoUpdate()"
            ></b-form-input>
          </b-form-group>
        </form>


        <hr />
        <b-button @click="userInfoUpdate()"> 수정 </b-button>
      </b-modal>
    <b-button @click="deleteUser">회원탈퇴</b-button>
    <!-- 독서 마라톤 관련 -->
    <b-row class="justify-content-center mt-4" v-if="marathon.value">  
      <p class="fs-5 fw-bold"><i class="fas fa-running text-primary"></i> 독서마라톤 <i class="fas fa-running text-primary"></i></p> <!-- 폰트어썸 아이콘 -->
      <b-progress
        :max="marathon.max"
        show-value
        class="mb-3 col-4 px-0"
        animated
        height="2rem"
      >
        <b-progress-bar :value="marathon.value">
          <span><strong>{{ marathon.value }} / {{ marathon.max }}</strong></span>
        </b-progress-bar>
      </b-progress>
      <p v-if="marathon.value > 0 && marathon.value===marathon.max">목표를 100% 달성했습니다! 목표를 갱신해주세요.<b-button size="sm" @click="updateGoal()">갱신</b-button></p>
      <p v-else>목표를 {{ marathon.value / marathon.max * 100  }}%를 달성했습니다!</p>
    </b-row>
    <div class="space"></div>

  </div>
  
</template>

<script>
import { mapState } from "vuex";
import { mapMutations } from "vuex";
import { mapActions } from "vuex";
import http from "@/config/http-common.js";


const userStore = "userStore";
export default {
  data() {
    return {
      myLinks: {         
        email: "",
        link: "", 
      },
      Img: 'https://dcimg4.dcinside.co.kr/viewimage.php?id=26b4ca33ebd339af&no=24b0d769e1d32ca73ceb84fa11d02831abf8dd37dbca42c9c56298cca8560265a7d8652086bfe4b9343f67919400852e8de3921a268bec3508394fa2d71e05d88e6ff22c1eb5a3174ddf19d920114166da3bce0caad0a7b3d2c982290a031781',
        // 임시 (미디어 업로드 데이터)에서 추후 가져올 것
      marathon: {
        value: 0, // 일단 기본값을 넣어서 서버에서 불러온 값이 없어도 표시되게
        max: 10, // max value를 백엔드 서버에서 현재 주지 않고 있어서 임의 표시
      },
      user: {
        profileImage: "",
        user_name: "",
        user_pw: "",
        user_pw_check: "",
        email: "",
        genre: "",
        goal: "",
        socialLink: "",
      },
      modalCheck: false,
    };
  },
  methods: {
    ...mapMutations(userStore, ["SET_IS_LOGIN", "SET_USER_INFO"]),
    ...mapActions(userStore, ["getUserInfo"]),
    // 회원탈퇴 기능
    deleteUser() {
      // 확인창
      if (confirm('탈퇴하시겠습니까?')) {
        http({
        method: 'delete',
        url: `/users/${this.userInfo.userId}`,
        })
        .then((response) => {
          console.log(response);
          // 로그아웃
          this.SET_IS_LOGIN(false);
          this.SET_USER_INFO(null);
          this.$router.push({
            path: "/",
          })
          alert('탈퇴가 완료되었습니다')
        })
        .catch((err) => {
          console.dir(err);
        })
      } else {
        alert('탈퇴가 취소되었습니다')
      }
    },
    getUserData() { // 기존 정보 가져오기
      this.modalCheck = true
      this.user.user_name = this.userInfo.userName
      this.user.email = this.userInfo.email
      this.user.genre = this.userInfo.genre
      this.user.goal = this.userInfo.goal
      this.user.socialLink = this.userInfo.socialLink
      this.user.profileImage = this.userInfo.profileImage
      
    },
    checkInfo() { // 입력값 유효성 체크
      console.log(this.user.profileImage)
      if(this.user.user_pw != this.user.user_pw_check) {
        alert('비밀번호를 동일하게 입력해주세요')
        return false
      // } else if(this.user.profileImage === null) {
      //   alert('프로필 이미지를 등록해주세요')
      //   return false
      } else if(this.user.user_pw === '' && this.user.user_pw_check == '') {
        alert('변경할 password를 입력해주세요')
        return false
      } else if(this.user.user_name === '') { // 변경할 유저네임 미입력시
        alert('변경할 username을 입력해주세요')
        return false
      } else if(this.user.user_name === this.userInfo.userName ) {
        if (confirm('username을 그대로 유지하시겠습니까?')) {
          return true
        } else {
          return false
        }
      } else {
        return true
      }

    },
    userInfoUpdate() { // 회원정보 수정
      if(this.checkInfo()) {
        http({
          headers: { 'Content-Type': 'multipart/form-data' }, // 확인 필요
          method: 'put',
          url: `/users/${this.userInfo.userId}`,
          data: {
            'password': this.user.user_pw,
            'userId': this.userInfo.userId,
            'userName': this.user.user_name,
            'email': this.user.email,
            'genre': this.user.genre,
            'goal': this.user.goal,
            'socialLink': this.user.socialLink,
            'profileImage': this.user.profileImage // 확인 필요
            }
          })
          .then((response) => {
            console.log(response)
            alert('회원정보 수정이 완료되었습니다')

            // 갱신된 정보 페이지에 바로 렌더링이 안되는 문제 수정 (기존 userStore 참조)
            let token = sessionStorage.getItem("access-token");
            this.getUserInfo(token)
            this.myLinks.email = this.userInfo.email, // 링크 갱신
            this.myLinks.link = this.userInfo.socialLink // 링크 갱신
           
            // 수정 완료 후 폼 비우고 모달창 꺼지게
            this.user.user_pw = ""
            this.user.user_pw_check = ""
            this.modalCheck = false
            
          })
          .catch((err) => {
            console.dir(err)
          })
      }
    },
    //독서마라톤 조회 기능
    getMarthonInfo() {
      http({
        method: "get",
        url: `/marathon/marathon-info/${this.userInfo.userId}`,
      })
        .then((response) => {
          console.log(response.data)
          this.marathon.value = response.data.nowPages
          this.marathon.max = response.data.goalPages
          
        })
        .catch((error) => {
          console.log(error.response)
          alert('독서 마라톤 정보 조회에 실패했습니다. 관리자에게 문의하세요')
        });
    },
    // 독서마라톤 목표(goalPages)수정 기능 --> 백엔드에 확인 필요
    updateGoal(){
       http({
        method: "put",
        url: `/marathon/${this}`,
      })
        .then((response) => {
          console.log(response)
        })
        .catch((error) => {
          console.log(error)
        });
    }
  },
  created() {
    // 링크 관련
    this.myLinks.email = this.userInfo.email,
    this.myLinks.link = this.userInfo.socialLink
    // 독서마라톤 관련 정보 가져오기
    this.getMarthonInfo()
    
  },
  computed: {
    ...mapState(userStore, ["userInfo"]),
  },
};
</script>

<style>
.space {
  height: 250px;
}
</style>
